import divider from '@/components/divider.vue';

//引入网络请求
import $H from '@/common/request.js';
import $C from '@/common/config.js';
import $U from '@/common/util.js';

export default {
	components: {
		divider
	},

	data() {
		return {
			background: {
				backgroundColor: '#fff',
			},
			index: 1,
			showDetail: false,
			showIndex: -1,
			show: false,
			type: 1,
			page: 1,
			reload: true,
			loadMore: false,
			status: 'more',
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多'
			},
			listData: [],
			mingxiList: [],
			nodata: false,
			scrollH:0

		}
	},

	onLoad() {

		uni.getSystemInfo({
			success: (res) => {
				console.log(res.windowHeight)

				if (620 < res.windowHeight <= 688) {
					this.scrollH = 40
				}

				if (res.windowHeight <= 688) {
					this.scrollH = 40
				}

				if (688 < res.windowHeight <= 730) {
					this.scrollH = 40
				}

				if (730 < res.windowHeight <= 810) {
					this.scrollH = 40
				}

				if (810 < res.windowHeight <= 850) {
					this.scrollH = 80
				}

				if (res.windowHeight > 814) {

					this.scrollH = 80
				}
			}
		});

		this.getData()
	},

	onReachBottom() {
		this.loadMore = true;
		this.page++;
		if (this.status == 'noMore') {
			return;
		}
		this.status = 'loading';
		this.getData()
	},

	onPullDownRefresh() {
		this.reload = true;
		this.page = 1;
		this.status = 'more';
		this.getData()
	},

	methods: {

		toMingXi(id) {
			uni.navigateTo({
				url: '../yongJinMingXi/yongJinMingXi?id=' + id + "&type=3"
			})
		},


		getData() {

			let data = {
				type: this.type,
				size: 10,
				current: this.page,
			}

			// 发送到服务端
			$H.get("/merchant_task/page/all", data).then((res) => {
				uni.stopPullDownRefresh()
				//请求成功
				console.log(res)
				let list = res.data.records

				let pages = res.data.pages;

				console.log(this.page)
				if (pages > 1 && this.page <= pages) {
					this.listData = this.reload ? list : this.listData.concat(list);
					this.nodata = false;
				} else if (pages == 0) {
					this.listData = [];
					this.nodata = true;
					this.status = 'noMore'

				} else if (pages == 1) {
					this.listData = list
					this.status = 'noMore'
					this.nodata = false;
				} else {
					this.status = 'noMore'
				}

				this.reload = false;


			}).catch((e) => {
				uni.stopPullDownRefresh()
				//请求失败
				console.log("失败" + e)
			})
		},

		getTask(id) {

			let data = {
				merchant_task_id: id
			}

			// 发送到服务端
			$H.post("/merchant_receive_task", data).then((res) => {

				//请求成功
				uni.showModal({
					title: "领取成功",
					confirmColor: "#FF7200",
					showCancel: true,
					success: (res) => {
						if (res.confirm) {
							this.page = 1
							this.reload = true;
							this.status = 'more';
							this.getData()
						}
					}
				})


			}).catch((e) => {

				//请求失败
				console.log("失败" + e)
			})
		},


		switchType(num) {
			this.index = num;
			this.type = num
			this.page = 1
			this.reload = true;
			this.status = 'more';
			this.getData()

		},
		changeShow(ind) {
			this.showIndex = ind;
			this.showDetail = !this.showDetail
		}
	}
}
